@import "styles/shared.scss";

.MoviesList {
  display: flex;
  flex-wrap: wrap;
  border-color: coral;
  list-style: none;
}

.MovieCard {
  width: 220px;
  margin: 10px;
  padding: 10px;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}

img {
  height: auto;
}
